// Contenedor principal
.login-container {
  height: 100vh;  // Altura completa de la pantalla
  width: 100vw;  // Ancho completo de la pantalla
  display: flex;  // Usamos Flexbox para centrar el contenido
  justify-content: center;  // Centra horizontalmente
  align-items: center;  // Centra verticalmente
  background-image: url('../../assets/img/fondo.jpg');  // Imagen de fondo
  background-size: cover;  // Asegura que la imagen cubra toda el rea sin distorsionar
  background-position: center;  // Centra la imagen de fondo
  background-repeat: no-repeat;  // No repetir la imagen de fondo
  background-color: #000;  // Color de fondo si la imagen no se carga
}

// Contenido principal del login
.login-content {
  background: rgba(255, 255, 255, 0.9);  // Fondo blanco semitransparente
  border-radius: 15px;  // Bordes redondeados
  padding: 40px;  // Ms relleno interno
  width: 100%;  // Ocupa todo el ancho disponible
  max-width: 500px;  // Mximo ancho de 500px para no exceder el tamao
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.3);  // Sombra ms grande para darle un efecto 3D
}

// Estilo para la cabecera del login
.login-header {
  text-align: center;  // Centra el texto de la cabecera
}

.login-logo {
  width: 100px;  // Tamao del logo
  height: 100px;  // Tamao del logo
  margin-bottom: 15px;  // Espacio debajo del logo
}

.login-title {
  font-size: 3rem;  // Tamao del ttulo principal
  margin: 0;  // Elimina el margen por defecto
}

.sos-text {
  font-weight: bold;  // Poner "Sos" en negrita
  color: #333;  // Color oscuro para "Sos"
}

.nine-eleven-text {
  font-weight: bold;  // Poner "911" en negrita
  color: #00ACAC;  // Color verde azulado para "911"
}

.login-subtitle {
  font-size: 1.2rem;  // Aumentamos el tamao del subttulo
  margin-bottom: 25px;  // Ms espacio debajo del subttulo
  color: #666;  // Color gris para el subttulo
}

// Estilo para las etiquetas y inputs
.login-label {
  font-size: 2.5rem;  // Aumentamos el tamao de las etiquetas (Usuario, Contrasea)
  color: #333;  // Color oscuro
  font-weight: bold;  // Hacemos las etiquetas en negrita
  margin-bottom: 10px;  // Espacio debajo de las etiquetas
  display: block;  // Asegura que las etiquetas ocupen una lnea completa
}

// Estilo de los inputs (Usuario, Contrasea)
.login-input {
  padding: 20px;  // Aumentamos el relleno interno para hacer los inputs ms grandes
  border-radius: 8px;  // Bordes redondeados
  border: 1px solid #ddd;  // Borde gris claro
  font-size: 1.6rem;  // Aumentamos el tamao de la fuente dentro de los inputs
  width: 100%;  // Los inputs ocupan todo el ancho disponible
  height: 55px;  // Aumentamos la altura de los inputs
  margin-bottom: 20px;  // Ms espacio entre los inputs
}

// Estilo del botn de inicio de sesin
.login-content .btn-primary {
  background-color: #00ACAC;  // Color de fondo inicial
  border: none;  // Sin borde
  font-size: 19px;  // Aumentamos el tamao de la fuente del botn (ms grande)
  padding: 10px;  // Tamao del relleno ajustado para hacerlo ms grande
  border-radius: 8px;  // Bordes redondeados
  transition: background-color 0.3s ease;  // Transicin solo del color de fondo
  width: 100%;  // Aseguramos que ocupe el ancho completo
}

// Efecto hover en el botn
.login-content .btn-primary:hover {
  background-color: #027d7d;  // El color de fondo cambia cuando pasa el ratn
  cursor: pointer;  // Asegura que el cursor sea un puntero
}

// Estilo del texto de registro
.login-register-text {
  font-size: 1.1rem;  // Tamao del texto de registro
  color: #333;  // Color oscuro
}

// Estilo para los enlaces de registro
.login-link {
  color: #00ACAC;  // Color verde azulado para los enlaces
  text-decoration: none;  // Sin subrayado
  font-weight: bold;  // Enlace en negrita
}

.login-link:hover {
  text-decoration: underline;  // Subrayado cuando el ratn pasa por encima
}

  // Media Queries para hacerlo responsive
  @media (max-width: 768px) {
    .login-container {
      height: 100vh;  // Aseguramos que la altura sea siempre 100% de la pantalla
    }
  
    .login-content {
      max-width: 90%;  // El contenedor ocupa el 90% del ancho
      padding: 20px;  // Reducimos el relleno en pantallas ms pequeas
    }
  
    .login-title {
      font-size: 2rem;  // Reducimos el tamao del ttulo en pantallas pequeas
    }
  
    .login-input {
      font-size: 1.4rem;  // Reducimos el tamao de la fuente en los inputs
      height: 50px;  // Reducimos la altura de los inputs
    }
  
    .btn-primary {
      font-size: 1.4rem;  // Reducimos el tamao del botn
    }
  }
  
  // Media Query para pantallas de 425px a 768px (tpicos de tabletas en modo retrato)
  @media (max-width: 425px) {
    .login-container {
      height: 100vh;  // Aseguramos que la altura ocupe toda la pantalla
    }
  
    .login-content {
      max-width: 90%;  // El contenedor ocupa el 90% del ancho
      padding: 15px;  // Reducimos ms el relleno para adaptarnos a pantallas ms pequeas
    }
  
    .login-input {
      font-size: 1.3rem;  // Reducimos un poco el tamao de los inputs
      height: 45px;  // Reducimos ms la altura de los inputs
    }
  
    .btn-primary {
      font-size: 1.3rem;  // Reducimos el tamao del botn
    }
  }
  
  // Media Query para pantallas de 375px a 768px (pantallas muy pequeas como celulares)
  @media (max-width: 375px) {
    .login-container {
      height: 100vh;  // Aseguramos que ocupe toda la pantalla
    }
  
    .login-content {
      max-width: 85%;  // Reducimos el ancho mximo al 85%
      padding: 10px;  // Reducimos el relleno an ms
    }
  
    .login-input {
      font-size: 1.3rem;  // Reducimos el tamao de la fuente
      height: 45px;  // Ajustamos la altura de los inputs
    }
  
    .btn-primary {
      font-size: 1.3rem;  // Reducimos el tamao del botn
    }
  }
  
  // Media Query para pantallas de 320px a 768px (pantallas pequeas como celulares compactos)
  @media (max-width: 320px) {
    .login-container {
      height: 100vh;  // Aseguramos que ocupe toda la pantalla
    }
  
    .login-content {
      max-width: 80%;  // El contenedor ocupa el 80% del ancho
      padding: 8px;  // Reducimos al mximo el relleno para adaptarse mejor
    }
  
    .login-input {
      font-size: 1.2rem;  // Reducimos el tamao de la fuente an ms
      height: 40px;  // Reducimos la altura de los inputs
    }
  
    .btn-primary {
      font-size: 1.3rem;  // Reducimos el tamao del botn
    }
  }
  