/* SCSS Styles (registro.scss): */
/*-----------------------------------------*/
// Variables globales (puedes definir colores y valores reutilizables)
$primary-color: #00acac; // Color principal usado en botones y textos destacados
$secondary-color: #008b8b; // Color secundario para efectos hover
$background-light: #f0f4f8; // Color de fondo claro
$text-dark: #333; // Color de texto oscuro
$white: #ffffff; // Color blanco

// Contenedor externo
.registro-outer-container {
  display: flex; // Flexbox para centrar contenido
  justify-content: center; // Alinear contenido horizontalmente al centro
  align-items: center; // Alinear contenido verticalmente al centro
  min-height: 100vh; // Altura mnima de toda la ventana
  background: #02c3b0; // Color de fondo general
  padding: 1rem; // Espaciado interno alrededor del contenedor
}

// Contenedor principal
.registro-container {
  display: flex; // Mostrar los hijos en fila
  width: 90%; // Ancho relativo al viewport
  max-width: 1200px; // Ancho mximo fijo
  border-radius: 16px; // Bordes redondeados
  overflow: hidden; // Ocultar contenido que se desborde
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2); // Sombra del contenedor
  background-color: $white; // Fondo blanco

  @media (max-width: 768px) {
    flex-direction: column; // Cambiar a columna en pantallas pequeas
    height: auto; // Ajustar altura automticamente
  }
}

// Contenedor izquierdo
.left-container {
  flex: 1; // Ocupa la mitad del espacio disponible
  background: #026b6b; // Color de fondo especfico
  display: flex; // Flexbox para contenido interno
  justify-content: center; // Centrar horizontalmente el contenido
  align-items: center; // Centrar verticalmente el contenido
  flex-direction: column; // Apilar los elementos en columna
  color: $white; // Texto en color blanco
  text-align: center; // Centrar texto
  padding: 2rem; // Espaciado interno

  .welcome-content {
    max-width: 80%; // Limitar el ancho del contenido
  }

  .welcome-image {
    width: 120px; // Ancho fijo para la imagen
    margin-bottom: 1rem; // Espacio inferior

    @media (max-width: 768px) {
      width: 100px; // Reducir ancho en pantallas pequeas
    }
  }

  .welcome-title {
    font-size: 2.5rem; // Tamao de fuente
    margin-bottom: 0.5rem; // Espacio inferior
    font-weight: bold; // Texto en negrita

    @media (max-width: 768px) {
      font-size: 2rem; // Ajustar tamao en pantallas pequeas
    }
  }

  .welcome-text {
    font-size: 1.2rem; // Tamao del texto
    line-height: 1.6; // Altura de lnea
    margin-bottom: 1rem; // Espacio inferior

    @media (max-width: 768px) {
      font-size: 1rem; // Reducir tamao en pantallas pequeas
    }
  }

  .welcome-person-image {
    width: 180px; // Ancho fijo
    border-radius: 100px; // Convertir en un crculo
    margin-top: 1rem; // Espacio superior

    @media (max-width: 768px) {
      width: 140px; // Reducir tamao en pantallas pequeas
    }
  }
}

// Contenedor derecho
.right-container {
  flex: 1; // Ocupa la mitad del espacio disponible
  background: #f9f9f9; // Fondo claro
  padding: 3rem; // Espaciado interno
  display: flex; // Flexbox
  flex-direction: column; // Apilar elementos
  justify-content: center; // Centrar contenido verticalmente

  @media (max-width: 768px) {
    padding: 2rem; // Reducir espaciado en pantallas pequeas
  }

  .titulo {
    text-align: center; // Centrar texto
    font-size: 2rem; // Tamao de fuente
    margin-bottom: 1.5rem; // Espacio inferior
    color: $primary-color; // Color destacado

    @media (max-width: 768px) {
      font-size: 1.5rem; // Ajustar tamao en pantallas pequeas
    }
  }

  .form-group {
    margin-bottom: 1.2rem; // Espaciado entre grupos de formulario
  }

  .etiqueta {
    display: block; // Elemento en bloque
    margin-bottom: 0.5rem; // Espacio inferior
    font-weight: bold; // Texto en negrita
    color: $text-dark; // Color oscuro

    @media (max-width: 768px) {
      font-size: 0.9rem; // Ajustar tamao en pantallas pequeas
    }
  }

  .entrada {
    width: 100%; // Ocupar todo el ancho
    padding: 0.75rem; // Espaciado interno
    border: 1px solid #ccc; // Borde gris
    border-radius: 6px; // Bordes redondeados

    @media (max-width: 768px) {
      padding: 0.6rem; // Reducir espaciado en pantallas pequeas
    }
  }

  .checkbox-group {
    display: flex; // Mostrar en fila
    align-items: flex-start; // Alinear verticalmente
    gap: 0.5rem; // Espaciado entre elementos
    margin-bottom: 1.5rem; // Espacio inferior

    @media (max-width: 768px) {
      flex-direction: column; // Cambiar a columna en pantallas pequeas
    }
  }

  .boton {
    width: 100%; // Botn ocupa todo el ancho
    padding: 0.75rem; // Espaciado interno
    background-color: $primary-color; // Color de fondo
    color: $white; // Texto en blanco
    border: none; // Sin borde
    border-radius: 6px; // Bordes redondeados
    cursor: pointer; // Cambiar el cursor al pasar encima

    &:hover {
      background-color: $secondary-color; // Cambiar color al pasar el mouse
    }

    @media (max-width: 768px) {
      padding: 0.6rem; // Reducir espaciado en pantallas pequeas
    }
  }

  .nine-eleven-text {
    font-weight: bold; // Texto en negrita
    color: #00acac; // Color especfico
  }

  .texto-enlace {
    text-align: center; // Centrar texto
    margin-top: 1rem; // Espacio superior

    .enlace {
      color: $primary-color; // Color de enlace
      text-decoration: none; // Sin subrayado

      &:hover {
        text-decoration: underline; // Subrayar al pasar el mouse
      }
    }
  }
}

// Estilos para el modal
.modal-overlay {
  position: fixed; // Fijar posicin
  top: 0; // Desde la parte superior
  left: 0; // Desde la parte izquierda
  width: 100%; // Ocupa todo el ancho
  height: 100%; // Ocupa toda la altura
  background: rgba(0, 0, 0, 0.5); // Fondo semitransparente
  display: flex; // Flexbox
  justify-content: center; // Centrar horizontalmente
  align-items: center; // Centrar verticalmente
}

.modal {
  background: $white; // Fondo blanco
  padding: 2rem; // Espaciado interno
  border-radius: 8px; // Bordes redondeados
  text-align: center; // Centrar texto

  @media (max-width: 768px) {
    padding: 1.5rem; // Reducir espaciado en pantallas pequeas
  }

  .modal-texto {
    margin-bottom: 1rem; // Espacio inferior
    font-size: 1.2rem; // Tamao de texto

    @media (max-width: 768px) {
      font-size: 1rem; // Reducir tamao en pantallas pequeas
    }
  }

  .boton-cerrar {
    background-color: $primary-color; // Color de fondo
    color: $white; // Texto en blanco
    border: none; // Sin borde
    padding: 0.75rem; // Espaciado interno
    border-radius: 6px; // Bordes redondeados
    cursor: pointer; // Cambiar cursor

    &:hover {
      background-color: $secondary-color; // Cambiar color al pasar el mouse
    }

    @media (max-width: 768px) {
      padding: 0.6rem; // Reducir espaciado en pantallas pequeas
    }
  }
}
